<template>
    <div class="login">
        <div class="mask"></div>
            <van-nav-bar fixed class="nav-bar" :border="true">
                <template #left>
                    <div class="logo iconfont icon-coffee"></div>
                    <div class="logo-title">Aster铺</div>
                </template>
                <template #right>
                    <div class="right-txt" @click="goPage">先逛一逛 </div>
                </template>
            </van-nav-bar>
        <div class="login-box">
            <div class="welcome">欢迎回来！</div>
            <div class="login-desc">Please login to your accounts</div>
            <div class="formInfo">
                <van-form>
                    <van-field
                        v-model="userInfo.phone"
                        name="手机号"
                        label="手机号"
                        placeholder="手机号"
                        autocomplete="off"
                    />
                    <van-field
                        v-model="userInfo.password"
                        :type="isLoginPassword ? 'password' : 'text'"
                        name="密码"
                        label="密码"
                        placeholder="密码"
                        :right-icon="isLoginPassword ? 'closed-eye' : 'eye-o'"
                        autocomplete="off"
                        @click-right-icon="toggleLoginPasswordType"
                    />
                    <div class="forgot">
                        <span @click="forgotPassword"> 忘记密码? </span>
                    </div>
                    <div class="login-btn">
                        <van-button round block color="#07c160" type="info" native-type="submit" @click="login">
                        登录
                        </van-button>
                    </div>
                </van-form>
            </div>
            <van-button type="info" round class="register" @click="registerInfo">注册</van-button>
            <van-popup v-model="show" closeable round position="bottom">
                <div class="register-title">注册</div>
                <van-form class="register-van-form">
                    <van-field
                        v-model="userRegisterInfo.phone"
                        name="手机号"
                        label="手机号"
                        placeholder="手机号"
                        autocomplete="off"
                    />
                    <van-field
                        v-model="userRegisterInfo.nickName"
                        name="用户名"
                        label="用户名"
                        placeholder="用户名"
                        autocomplete="off"
                    />
                    <van-field
                        v-model="userRegisterInfo.password"
                        :type="isRegisterPassword ? 'password' : 'text'"
                        name="密码"
                        label="密码"
                        placeholder="密码"
                        :right-icon="isRegisterPassword ? 'closed-eye' : 'eye-o'"
                        @click-right-icon="toggleRegisterPasswordType"
                        autocomplete="off"
                    />
                    <div class="login-btn">
                        <van-button round block type="info" native-type="submit" @click="register">
                        注册
                        </van-button>
                    </div>
                </van-form>
            </van-popup>
        </div>
    </div>
</template>

<script>
    import '../assets/less/login.less'
    import '../assets/font/iconfont.css'

    // 导入表单验证模块
    import {verifyForm} from '../assets/js/verifyForm.js'

    export default {
        name: 'Login',
        data() {
            return {
                //用户登录信息
                userInfo:{
                    phone: '',
                    password: ''
                },
                //用户注册信息
                userRegisterInfo:{
                    phone: '',
                    password: '',
                    nickName: ''
                },
                // 是否显示注册框
                show: false,

                // 切换登录密码框的type
                isLoginPassword: true,
                // 切换注册密码框的type
                isRegisterPassword: true
            };
        },
        methods: {
            //切换注册框
            registerInfo(){
                this.show = true;
            },
            //切换登录密码框的类型
            toggleLoginPasswordType(){
                this.isLoginPassword = !this.isLoginPassword
            },
            // 切换注册密码框的类型
            toggleRegisterPasswordType(){
                this.isRegisterPassword = !this.isRegisterPassword
            },
            //注册
            register(){
                
                //构造表单验证信息
                let formData = {
                    phone: {
                        value: this.userRegisterInfo.phone,
                        errorMsg: '手机号格式不正确',
                        reg: /^1[0-9]{10}$/
                    },
                    password: {
                        value: this.userRegisterInfo.password,
                        errorMsg: '密码由数字字母下划线组合(6-16字符)',
                        reg: /^[A-Za-z]\w{5,15}$/
                    },
                    nickName: {
                        value: this.userRegisterInfo.nickName,
                        errorMsg: '昵称格式不正确',
                        reg: /^[\u4E00-\u9FA5A-Za-z0-9_]+$/
                    },
                }
                let isPass = verifyForm.verify(formData);
                if(isPass){
                    //  赋值用户注册信息
                    let userInfo = Object.assign({},this.userRegisterInfo);
                    userInfo.appkey = this.appkey;

                    this.$toast.loading({
                        message: '加载中...',
                        forbidClick: true,
                        duration: 0
                    })

                    //发起注册请求
                    this.axios({
                        method: 'POST',
                        url: '/register',
                        data: userInfo
                    }).then(res => {
                        //
                        this.$toast.clear();
                        if(res.data.code == 100){
                            this.show = false;
                            
                            
                        }else{
                            this.$toast(res.data.msg);
                        }
                    }).catch(err => {
                        this.$toast.clear();
                        
                    })
                    this.show = false;
                }
            },

            //登录
            login(){
                let formData = {
                    phone: {
                        value: this.userInfo.phone,
                        errorMsg: '手机号格式不正确',
                        reg: /^1[0-9]{10}$/
                    },
                    password: {
                        value: this.userInfo.password,
                        errorMsg: '密码格式不正确',
                        reg: /^[A-Za-z]\w{5,15}$/
                    },
                }
                let isPass = verifyForm.verify(formData);
                if(isPass){
                    //  赋值用户注册信息
                    let userInfo = Object.assign({},this.userInfo);
                    userInfo.appkey = this.appkey;

                    this.$toast.loading({
                        message: '加载中...',
                        forbidClick: true,
                        duration: 0
                    })

                    //发起注册请求
                    this.axios({
                        method: 'POST',
                        url: '/login',
                        data: userInfo
                    }).then(res => {
                        //
                        this.$toast.clear();
                        if(res.data.code == 200){
                            // 
                            localStorage.setItem('token',res.data.token);
                            this.$router.push({name: 'Home'});
                        }else{
                            this.$toast(res.data.msg);
                        }
                    }).catch(err => {
                        this.$toast.clear();
                        
                    })

                }
            },
            //忘记密码
            forgotPassword(){
                this.$router.push({name: 'FindPassword'});
            },
            //先逛一逛
            goPage(){
                this.$router.push({name: 'Home'});
            }
        },
    }

</script>
